.ContactsList {
  width: 600px;

  &__title {
    color: rgba(0, 0, 255, .8);
  }

  &__user-button--selected {
    background-color: #4d457b;
  }

  &__container {
    display: flex;
    gap: 20px;
  }

  &__list {
    list-style: none;
    padding: 0;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid rgba(0, 0, 255, .2);
    position: relative;

    &:hover {
      box-shadow: 8px 8px 2px 1px rgba(0, 0, 255, .2);
      cursor: pointer;
    }

    &:hover::after {
      content: attr(data-title);
      position: absolute; 
      left: -13%; top: 70%; 
      z-index: 1; 
      background: rgba(255,255,230,0.9); 
      font-family: Arial, sans-serif;
      font-size: 11px; 
      padding: 5px 10px; 
      border: 1px solid #333;
     }

    
  }

  &__button {
    background-color: #ffffff;
    color: #ff3626;
    border-radius: 10px;
    border: 1px solid;

    &:hover {
      box-shadow: 1px 1px 2px 1px rgba(115, 115, 115, 0.2);
      background-color: #ffb1a5;
    }
  }
}
